.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #333; /* Text color */
}

table {
  border-collapse: collapse;
  margin-bottom: 20px;
}

table td, table th {
  padding: 10px;
  border: 1px solid #89bfeb;
}

table th {
  font-weight: bold;
  background-color: #f2f2f2;
}

input[type="text"] {
  width: 80%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff; /* Button background color */
  color: #fff; /* Button text color */
  font-size: 16px;
}

button:hover {
  background-color: #0056b3; /* Button background color on hover */
}

.error-message {
  color: #ff0000; /* Error message color */
  font-size: 18px;
  margin-bottom: 20px;
}
